import { withRouter } from 'next/router'

const ActiveLink = ({ children, router, href }) => {
  const style = {
    border: router.pathname === href? '1px solid #141414' : 'none'
  }

  const handleClick = (e) => {
    e.preventDefault()
    router.push(href)
  }

  return (
    <a href={href} onClick={handleClick} style={style} className="btn">
      {children}
    </a>
  )
}

export default withRouter(ActiveLink)